.advantage {
    // padding-top: 40px;
    padding-bottom: 60px;
    background-color: #FFF5F6;

    @media screen and (min-width: 480px) {
        padding-bottom: 64px;
        padding-top: 20px;
    }
}

.advantage__list {
    @media screen and (min-width: 480px) {
        display: flex;
    }
}

.advantage__item {
    max-width: 230px;
    margin-bottom: 32px;

    @media screen and (min-width: 768px) {
        max-width: 159px;
        margin-right: 22px;
    }

    @media screen and (min-width: 1200px) {
        margin-right: 115px;
        max-width: 288px;
    }
}

.advantage__item:nth-last-child(1) {
    margin-bottom: 0;

    @media screen and (min-width: 1200px) {
        margin-right: 0;
    }
}

.dug::before {
    display: block;
    content: '';
    width: 33px;
    height: 50px;
    margin-bottom: 16px;
    background-image: url(../images/mobile/made-icon-mobile-1-1x.png);
    background-size: contain;
    background-repeat: no-repeat;

    @media (min-device-pixel-ratio:2),
    (min-resolution: 2dppx),
    (min-resolution: 192dpi) {
        background-image: url(../images/mobile/made-icon-mobile-1-2x.png);
    }

    @media screen and (min-width: 480px) {
        width: 50px;
        background-image: url(../images/tablet/made-icon-tablet-1-1x.png);

        @media (min-device-pixel-ratio:2),
        (min-resolution: 2dppx),
        (min-resolution: 192dpi) {
            background-image: url(../images/tablet/made-icon-tablet-1-2x.png);
        }
    }
}

.bucket::before {
    display: block;
    content: '';
    width: 50px;
    height: 50px;
    margin-bottom: 16px;
    background-image: url(../images/mobile/made-icon-mobile-2-1x.png);
    background-size: contain;
    background-repeat: no-repeat;

    @media (min-device-pixel-ratio:2),
    (min-resolution: 2dppx),
    (min-resolution: 192dpi) {
        background-image: url(../images/mobile/made-icon-mobile-2-2x.png);
    }

    @media screen and (min-width: 480px) {

        background-image: url(../images/tablet/made-icon-tablet-2-1x.png);

        @media (min-device-pixel-ratio:2),
        (min-resolution: 2dppx),
        (min-resolution: 192dpi) {
            background-image: url(../images/tablet/made-icon-tablet-2-2x.png);
        }
    }
}

.heart::before {
    display: block;
    content: '';
    width: 44px;
    height: 50px;
    margin-bottom: 16px;
    background-image: url(../images/mobile/made-icon-mobile-3-1x.png);
    background-size: contain;
    background-repeat: no-repeat;

    @media (min-device-pixel-ratio:2),
    (min-resolution: 2dppx),
    (min-resolution: 192dpi) {
        background-image: url(../images/mobile/made-icon-mobile-3-2x.png);
    }

    @media screen and (min-width: 480px) {

        background-image: url(../images/tablet/made-icon-tablet-3-1x.png);

        @media (min-device-pixel-ratio:2),
        (min-resolution: 2dppx),
        (min-resolution: 192dpi) {
            background-image: url(../images/tablet/made-icon-tablet-3-2x.png);
        }
    }
}

.advantage__number {
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 36px;
    line-height: 1.14;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: var(--color-accent);
    height: 56px;
    margin-bottom: 4px;

    @media screen and (min-width: 768px) {
        font-size: 48px;
        margin-bottom: 13px;
    }

    @media screen and (min-width: 1200px) {
        font-size: 58px;
        margin-bottom: 16px;
    }
}

.advantage__text {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 170%;


    @media screen and (min-width: 768px) {
        font-size: 14px;
    }

    @media screen and (min-width: 1200px) {
        font-size: 16px;
    }
}